"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { ArrowLeft, Eye, Save, X, AlertCircle } from "lucide-react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { useToast } from "@/hooks/use-toast";
import { PreviewModal } from "@/components/admin/preview-modal";
import type { IProduct, IProductData } from "@/models/Product";

export default function EditProductPage({ params }: { params: { id: string } }) {
    const router = useRouter();
    const { toast } = useToast();
    const [product, setProduct] = useState<IProduct | null>(null);
    const [originalProduct, setOriginalProduct] = useState<IProduct | null>(null);
    const [loading, setLoading] = useState(true);
    const [saving, setSaving] = useState(false);
    const [showPreview, setShowPreview] = useState(false);
    const [showCancelDialog, setShowCancelDialog] = useState(false);

    useEffect(() => {
        fetchProduct();
    }, [params.id]);

    const fetchProduct = async () => {
        try {
            const response = await fetch(`/api/admin/products/${params.id}`);
            const data = await response.json();
            setProduct(data.product);
            setOriginalProduct(data.product);
        } catch (error) {
            console.error("Error fetching product:", error);
            toast({
                title: "Error",
                description: "Failed to load product",
                variant: "destructive",
            });
        } finally {
            setLoading(false);
        }
    };

    const hasUnsavedChanges = () => {
        if (!product || !originalProduct) return false;
        return JSON.stringify(product) !== JSON.stringify(originalProduct);
    };

    const handleSave = async () => {
        if (!product) return;

        setSaving(true);
        try {
            const response = await fetch(`/api/admin/products/${params.id}`, {
                method: "PUT",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(product),
            });

            if (!response.ok) throw new Error("Failed to save");

            const data = await response.json();
            setProduct(data.product);
            setOriginalProduct(data.product);

            toast({
                title: "Success",
                description: "Product updated successfully",
            });
        } catch (error) {
            toast({
                title: "Error",
                description: "Failed to save product",
                variant: "destructive",
            });
        } finally {
            setSaving(false);
        }
    };

    const handleCancel = () => {
        if (hasUnsavedChanges()) {
            setShowCancelDialog(true);
        } else {
            router.push("/admin/products");
        }
    };

    const confirmCancel = () => {
        setProduct(originalProduct);
        setShowCancelDialog(false);
        router.push("/admin/products");
    };

    if (loading) {
        return (
            <div className="flex items-center justify-center h-96">
                <p className="text-muted-foreground">Loading product...</p>
            </div>
        );
    }

    if (!product) {
        return (
            <div className="flex flex-col items-center justify-center h-96 space-y-4">
                <AlertCircle className="h-12 w-12 text-muted-foreground" />
                <p className="text-muted-foreground">Product not found</p>
                <Button asChild>
                    <Link href="/admin/products">Back to Products</Link>
                </Button>
            </div>
        );
    }

    return (
        <>
            <div className="space-y-6">
                <div className="flex items-center justify-between">
                    <div className="flex items-center gap-4">
                        <Button variant="ghost" size="icon" asChild>
                            <Link href="/admin/products">
                                <ArrowLeft className="h-4 w-4" />
                            </Link>
                        </Button>
                        <div>
                            <h1 className="text-3xl font-bold">Edit Product</h1>
                            <p className="text-muted-foreground mt-1">{product.name}</p>
                        </div>
                    </div>

                    <div className="flex items-center gap-2">
                        <Button
                            variant="outline"
                            onClick={() => setShowPreview(true)}
                            disabled={!hasUnsavedChanges()}
                        >
                            <Eye className="mr-2 h-4 w-4" />
                            Preview Changes
                        </Button>
                        <Button
                            variant="outline"
                            onClick={handleCancel}
                        >
                            <X className="mr-2 h-4 w-4" />
                            Cancel
                        </Button>
                        <Button
                            onClick={handleSave}
                            disabled={!hasUnsavedChanges() || saving}
                        >
                            <Save className="mr-2 h-4 w-4" />
                            {saving ? "Saving..." : "Save Changes"}
                        </Button>
                    </div>
                </div>

                {hasUnsavedChanges() && (
                    <div className="bg-yellow-50 dark:bg-yellow-900/20 border border-yellow-200 dark:border-yellow-800 rounded-lg p-4">
                        <p className="text-sm text-yellow-800 dark:text-yellow-200">
                            You have unsaved changes. Don't forget to save!
                        </p>
                    </div>
                )}

                <div className="grid gap-6 md:grid-cols-2">
                    <div className="space-y-4">
                        <div>
                            <Label htmlFor="name">Product Name</Label>
                            <Input
                                id="name"
                                value={product.name}
                                onChange={(e) => setProduct({ ...product, name: e.target.value })}
                            />
                        </div>

                        <div>
                            <Label htmlFor="price">Price ($)</Label>
                            <Input
                                id="price"
                                type="number"
                                step="0.01"
                                value={product.price}
                                onChange={(e) => setProduct({ ...product, price: parseFloat(e.target.value) })}
                            />
                        </div>

                        <div>
                            <Label htmlFor="category">Category</Label>
                            <Select
                                value={product.category}
                                onValueChange={(value) => setProduct({ ...product, category: value as any })}
                            >
                                <SelectTrigger>
                                    <SelectValue />
                                </SelectTrigger>
                                <SelectContent>
                                    <SelectItem value="Bear">Bear</SelectItem>
                                    <SelectItem value="Bunny">Bunny</SelectItem>
                                    <SelectItem value="Magical">Magical</SelectItem>
                                    <SelectItem value="Dinosaur">Dinosaur</SelectItem>
                                    <SelectItem value="Animal">Animal</SelectItem>
                                </SelectContent>
                            </Select>
                        </div>

                        <div>
                            <Label htmlFor="brand">Brand</Label>
                            <Input
                                id="brand"
                                value={product.brand}
                                onChange={(e) => setProduct({ ...product, brand: e.target.value })}
                            />
                        </div>

                        <div>
                            <Label htmlFor="ageRange">Age Range</Label>
                            <Input
                                id="ageRange"
                                value={product.ageRange}
                                onChange={(e) => setProduct({ ...product, ageRange: e.target.value })}
                            />
                        </div>
                    </div>

                    <div className="space-y-4">
                        <div>
                            <Label htmlFor="description">Short Description</Label>
                            <Textarea
                                id="description"
                                value={product.description}
                                onChange={(e) => setProduct({ ...product, description: e.target.value })}
                                rows={3}
                            />
                        </div>

                        <div>
                            <Label htmlFor="longDescription">Long Description</Label>
                            <Textarea
                                id="longDescription"
                                value={product.longDescription}
                                onChange={(e) => setProduct({ ...product, longDescription: e.target.value })}
                                rows={5}
                            />
                        </div>

                        <div>
                            <Label htmlFor="materials">Materials</Label>
                            <Input
                                id="materials"
                                value={product.materials}
                                onChange={(e) => setProduct({ ...product, materials: e.target.value })}
                            />
                        </div>

                        <div>
                            <Label htmlFor="care">Care Instructions</Label>
                            <Textarea
                                id="care"
                                value={product.care}
                                onChange={(e) => setProduct({ ...product, care: e.target.value })}
                                rows={2}
                            />
                        </div>
                    </div>
                </div>
            </div>

            <PreviewModal
                open={showPreview}
                onOpenChange={setShowPreview}
                product={product}
                originalProduct={originalProduct}
            />

            <AlertDialog open={showCancelDialog} onOpenChange={setShowCancelDialog}>
                <AlertDialogContent>
                    <AlertDialogHeader>
                        <AlertDialogTitle>Unsaved Changes</AlertDialogTitle>
                        <AlertDialogDescription>
                            You have unsaved changes. Are you sure you want to leave? All changes will be lost.
                        </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                        <AlertDialogCancel>Continue Editing</AlertDialogCancel>
                        <AlertDialogAction onClick={confirmCancel} className="bg-destructive text-destructive-foreground">
                            Discard Changes
                        </AlertDialogAction>
                    </AlertDialogFooter>
                </AlertDialogContent>
            </AlertDialog>
        </>
    );
}
